<template>
  <div id="app">
      <ele-header :seller="seller"></ele-header>
      <ul class="nav">
          <li><router-link to='/'></router-link></li>
          <li><router-link to='/ratings'></router-link></li>
          <li><router-link to='/seller'></router-link></li>
      </ul>
      <router-view></router-view>
  </div>
</template>

<script>
//Header
import Header from './components/Header/Header'
//data.json
const shop = require('./api/data.json');
export default {
  name: 'app',
  data(){
      return {
          seller : {} //
      }
  },
  created(){
      //data
      this.seller = shop.seller;
  },
  components : {
      "ele-header" : Header //Header
  }
}
</script>

<style lang="less" scoped>
    .nav {
        display: flex;
        justify-content: space-around;
        height: 40px;
        line-height: 40px;
        border-bottom: 1px solid rgba(7,17,27,.1);

        a {
            color :rgb(77,85,93);
            font-size: 14px;
            text-decoration: none;
            &.router-link-exact-active{
                color:rgb(240,20,20);
            }
        }
    }
</style>



